// html { height: 100vh; }
@font-face {
  font-family: SFDisplay;
  src: url("../styles/font/SFProDisplay.ttf");
}
#root {
  width: 100vw;
  height: 100vh;
}

body {
  color: var(--PRIMARY__CONTENT__COLOR);
  background-color: var(--PRIMARY__BG__COLOR);
}

html {
  height: 100vh;
  width: 100vw;
  overflow: unset;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
body {
  font-family: "SFDisplay", "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  background-color: var(--PRIMARY__BG__COLOR);
  .step__section {
    .header__step {
      width: 100vw;
      .am-steps-item-title {
        font-size: var(--fs__small);
        font-weight: var(--fw__normal);
        color: var(--PRIMARY__CONTENT__COLOR);
        white-space: nowrap;
        text-align: center;
      }
      .am-steps-item-tail {
        padding: 0 10px;
        // background: #60B44A;
      }
    }
    .content__step {
      .am-tabs-content-wrap {
        height: unset;
      }
      .am-tabs-tab-bar-wrap {
        display: none;
      }
    }
  }

  .center__item {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    &--right {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: flex-end;
    }
    &--left {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: flex-start;
    }
    &--center {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: center;
    }
  }

  .about__logo__block {
    padding: 8px 0;
  }
  .login__form {
    margin-top: 16px;
    padding: 4px 8px;
    .ant-form-item {
      margin-bottom: 0;
    }
    .input__component {
      border: 0;
      border-bottom: 1px solid var(--PRIMARY__BORDER__COLOR);
      background-color: transparent;
    }
    .login-form-forgot {
      color: var(--PRIMARY__CONTENT__COLOR);
    }
  }
  .common__form {
    margin-top: 16px;
    padding: 4px 8px;
    // Style component error
    .ant-form-item-has-error {
      .ant-form-item-control-input {
        border: 1px solid var(--DOWN__COLOR);
      }
    }
    .ant-form-item-control-input {
      border: 1px solid var(--PRIMARY__BORDER__COLOR);
      border-radius: 8px;
      overflow: hidden;
    }
    .ant-form-item-control-input-content {
      height: 56px;
      align-items: flex-end;
      display: flex;
      background-color: var(--INPUT__BG);
      .ant-input {
        &:focus {
          box-shadow: none;
        }
      }
    }
    .ant-form-item {
      margin-bottom: 0;
    }
    .common__form__input-label {
      position: absolute;
      top: 8px;
      left: 16px;
      opacity: 0.7;
      font-weight: var(--fw__medium);
    }
    .common__form__input-icon-select {
      position: absolute;
      right: 24px;
      top: 20px;
    }
    .input__component {
      border: 0;
      background-color: transparent;
      padding: 8px 16px;
      border-bottom: 1px solid var(--INPUT__BG);
    }
    .login-form-forgot {
      color: var(--PRIMARY__CONTENT__COLOR);
    }
  }
  .row__news {
    width: 100%;
    display: flex;
    flex-direction: row;
    .row__news--img {
      height: 100px;
      width: 100px;
      background-color: var(--SECOND__BG__COLOR);
    }
    .row__news--title {
      display: flex;
      height: 30px;
    }
  }
  .settings__layout {
    .ant-row {
      margin: 0 !important;
    }
  }
  .market__top--tab-view {
    .ant-tabs-tab {
      margin: 0;
      width: 25%;
      justify-content: center;
    }
    .ant-tabs-nav-list {
      width: 100%;
    }
    .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
      color: var(--PRIMARY__CONTENT__COLOR);
    }
    .ant-tabs-tab-btn {
      font-size: var(--fs__small);
    }
  }
}

.icon_center {
  margin: auto;
}
.header__navigation {
  padding: 8px 8px 8px 4px;
  .header__title {
    margin-bottom: 0;
  }
}
.settings__layout {
  .settings__userinfo {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    .settings__userinfo__username {
      margin-bottom: 0;
      // line-height: 52px;
      display: flex;
      align-items: center;
    }
  }
  .settings__title {
    margin: 0;
    padding-bottom: 0;
  }
  .promotion__list {
    height: 100px;
    width: 160px;
    max-width: 160px;
    background-image: linear-gradient(blue, var(--PRIMARY));
    background-color: var(--PRIMARY);
    margin: 0 4px;
    border-radius: 4px;
    color: white;
  }
}

.about__copyright {
  padding: 8px 0 0 16px;
}
.menu__layout {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.text-dot {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: calc(50vw - 9px);
}

.sticky_header_tabs {
  position: sticky;
  position: -webkit-sticky;
  top: -1px;
  z-index: 1;
  background-color: var(--PRIMARY__BG__COLOR);
  .rmc-tabs-tab-bar-top {
    border-bottom-color: var(--DIVIDER__COLOR);
  }
  .rmc-tabs-tab-bar-underline {
    border: 1px var(--PRIMARY__CONTENT__COLOR) solid;
    border-radius: 2px;
  }
}
.ant-tabs-ink-bar {
  background-color: var(--PRIMARY__CONTENT__COLOR);
}
button {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  padding: unset;
}
#chart-buysell {
  width: 100%;
}

.list {
  min-height: 44px;
  padding-top: 7px;
  padding-bottom: 7px;
}
.divider_border {
  background-color: var(--DIVIDER__VIEW__COLOR);
  height: 1px;
}

.icon_tabbar {
  width: var(--fs__big);
  height: var(--fs__big);
  opacity: 0.5;
  &.icon__active {
    opacity: 1;
  }
}

.footer button {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.modal-add-fav-stock .am-modal-transparent {
  .am-modal-content {
    background-color: var(--HEADER__BG__COLOR);
    padding-top: 0;
    .am-modal-body {
      padding: 0;
    }
  }
}

@media only screen and (min-width: 451px) {
  .modal-add-fav-stock .am-modal-transparent {
    width: 70vw;
  }
}
@media only screen and (max-width: 450px) {
  .modal-add-fav-stock .am-modal-transparent {
    width: calc(100vw - 44px);
  }
}

.search_screen .am-list-line {
  border-bottom: 1px solid var(--DIVIDER__COLOR) !important;
}

.fixed_header {
  position: fixed;
  top: 0;
  width: 100vw;
  background-color: var(--PRIMARY__BG__COLOR);
  z-index: 999;
}
.fixed_footer {
  position: fixed;
  bottom: 0;
  width: 100vw;
  background-color: var(--bottom__tab);
  z-index: 999;
}

// css devT define
.relative-icon-center {
  width: 30px;
  height: 30px;
  background: #f8f8f8;
  text-align: center;
  border-radius: 50%;
  position: relative;
}

.modal-loading {
  backdrop-filter: blur(16px);
}

.ant-notification-notice {
  border-radius: 8px !important;
  padding: 10px 12px !important;
}
.collapse-item-stock-owned .ant-collapse-header {
  padding: 5px 0px !important;
}

.collapse-item-stock-owned .ant-collapse-content-box {
  background-color: var(--SECOND__BG__COLOR);
}
.collapse-item-stock-owned .ant-collapse-header {
  padding: 5px 0px !important;
}

.collapse-item-stock-owned .ant-collapse-content-box {
  background-color: var(--SECOND__BG__COLOR);
}

.rmc-tabs-tab-bar-prevpage,
.rmc-tabs-tab-bar-nextpage {
  display: none !important;
}

.rmc-tabs-tab-bar-top {
  border-bottom-color: var(--DIVIDER__COLOR);
  border-bottom: 1px solid var(--DIVIDER__COLOR) !important
}

.view-loading-shinhan {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99999;
}

.loading-shinhan {
  display: none;
}
.loading-shinhan:after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 90px;
  height: 90px;
  border-top: 2px solid #ce9b51;
  border-right: 2px solid #ce9b51;
  border-radius: 50%;
  content: '';
  margin: -45px 0 0 -45px;
  -webkit-animation: rotate 1s linear infinite;
  animation: rotate 1s linear infinite;
  -moz-animation: rotate 1s linear infinite;
  -o-animation: rotate 1s linear infinite;
}

.modal-loading-shinhan {
  .ant-modal-content {
      border-color: transparent;
      background-color: transparent;
      box-shadow: none;
  }
  .ant-modal-body {
      background-color: transparent;
  }
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: transparent;
  color: transparent;
}

input {
  background-color: inherit;
}