// Style override cho bản mobile. Không cần quan tâm đến responesive cho web
.ant-drawer-header{
  background: var(--PRIMARY__BG__COLOR) !important;
}
.amd-tabs-item {
  white-space: normal;

  & > div {
    width: 100%;
  }
}

.ant-typography,
h1.ant-typography,
h2.ant-typography,
h3.ant-typography,
h4.ant-typography,
h5.ant-typography,
h6.ant-typography {
  color: var(--PRIMARY__CONTENT__COLOR);
  margin: 0;
}

h3.ant-typography {
  font-size: var(--fs__big);
  line-height: 1.5;
}
h4.ant-typography {
  font-size: var(--fs__big);
  line-height: 1.5;
}
h5.ant-typography {
  font-size: var(--fs__medium);
  line-height: 1.5;
}
h6.ant-typography {
  font-size: var(--fs_verySmall);
  line-height: 1.5;
  font-weight: normal;
  opacity: 0.7;
}
span.ant-typography {
  font-size: var(--fs__normal);
  line-height: 1.5;
}

.ant-list-split .ant-list-item {
  border-bottom-color: var(--SECOND__BG__COLOR);
}

.amd-tabs {
  background-color: var(--PRIMARY__BG__COLOR);
}
.anticon {
  color: var(--PRIMARY__CONTENT__COLOR);
}

.ant-input-affix-wrapper {
  background-color: transparent;
  border: none;
}

.ant-row {
  padding: 4px 8px 8px 4px;
}

.ant-list-item {
  padding: 8px 16px;
}

.ant-drawer .ant-drawer-content{
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.ant-drawer-header{
  border-bottom: 0;
}

.ant-drawer-title{
  color: #000000;
  font-weight: 600;
}


.ant-drawer-body {
  background-color: var(--PRIMARY__BG__COLOR);
  padding: 15px 24px;
}
.ant-input {
  background-color: transparent;
  color: var(--PRIMARY__CONTENT__COLOR);
  border: 1px solid var(--INPUT__BG);
  :placeholder-shown {
    color: var(--PLACEHODLER__COLOR);
  }
}
.ant-checkbox-wrapper {
  color: var(--PRIMARY__CONTENT__COLOR);
}

.am-tabs-tab-bar-wrap .am-tab-bar-bar::before {
  background-color: transparent!important;
}
.rmc-tabs .rmc-tabs-pane-wrap {
  overflow: hidden;
}
.am-tab-bar .am-tabs-pane-wrap {
  // overflow: hidden;
  .am-tab-bar-item {
    display: flex;
    flex-direction: column;
  }
}

.am-tabs-content-wrap {
  height: 100vh;
}
.am-list-item:not(:last-child) .am-list-line::after {
  background-color: transparent!important;
}

.modal-add-fav-stock {
  .am-list-item.am-list-item-active {
    background-color: var(--PRIMARY__BG__COLOR)!important;
  }
}
.am-list-item.am-textarea-item {
  background-color: var(--PRIMARY__BG__COLOR)
}

.am-tabs-tab-bar-wrap {
  .am-tab-bar-bar {
    background-color: var(--PRIMARY__BG__COLOR) !important;
    height: auto;
    padding-top: 9px;
    padding-bottom: 9px;
    border-top: none;
    box-shadow: 0 5px 10px var(--PRIMARY__CONTENT__COLOR);
    .am-tab-bar-tab-title {
      font-size: var(--fs__smallest);
    }
  }
}
.am-search {
  height: var(--height__search);
  .am-search-input {
    background-color: var(--HEADER__BG__COLOR);
    height: var(--height__search__input);
    input[type="search"] {
      color: var(--PRIMARY__CONTENT__COLOR);
      height: var(--height__search__input);
      font-size: var(--fs__normal);
    }
    .am-search-clear {
      width: var(--search__icon);
      height: var(--search__icon);
      background-size: var(--search__icon);
    }
    .am-search-synthetic-ph {
      height: var(--height__search__input);
      line-height: var(--height__search__input);
      .am-search-synthetic-ph-icon {
        width: var(--search__icon);
        height: var(--search__icon);
        background-size: var(--search__icon) auto;
      }
      .am-search-synthetic-ph-placeholder {
        font-size: var(--fs__normal);
      }
    }
  }
  .am-search-cancel {
    font-size: var(--fs__medium);
  }
}
.am-search.am-search-start .am-search-input input[type="search"] {
  padding: 0 40px 0 auto;
}

.ant-switch {
  background-color: #2f3542;
}
.ant-switch-checked {
  background-color: #1890ff;
}

.ma5 .ant-checkbox-checked .ant-checkbox-inner{
  background-color: var(--LINE__MA5);
  border-color: var(--LINE__MA5);
}

.ma10 .ant-checkbox-checked .ant-checkbox-inner{
  background-color: var(--LINE__MA10);
  border-color: var(--LINE__MA10);
}

.ma20 .ant-checkbox-checked .ant-checkbox-inner{
  background-color: var(--LINE__MA20);
  border-color: var(--LINE__MA20);
}

.ma30 .ant-checkbox-checked .ant-checkbox-inner{
  background-color: var(--LINE__MA30);
  border-color: var(--LINE__MA30);
}


.ant-dropdown-menu {
  background-color: transparent;
}

.ant-input-affix-wrapper:focus, .ant-input-affix-wrapper-focused {
  box-shadow: unset;
  background-color: transparent;
}
.ant-form-item-has-error .ant-input:focus, .ant-form-item-has-error .ant-input-affix-wrapper:focus, .ant-form-item-has-error .ant-input-focused, .ant-form-item-has-error .ant-input-affix-wrapper-focused {
  box-shadow: none;
  background-color: transparent;
}

.rmb__login__checkbox .ant-checkbox-checked .ant-checkbox-inner{
  background-color: var(--PRIMARY);
  border-color: var(--PRIMARY);
}

.am-tab-bar-item {
    display: flex;
    flex: 1;
}

.ant-divider-plain.ant-divider-with-text {
  color: var(--PRIMARY__CONTENT__COLOR);
}

.ant-divider-horizontal.ant-divider-with-text::before, .ant-divider-horizontal.ant-divider-with-text::after {
  border-top-color: var(--PRIMARY__BORDER__COLOR);
}

.am-modal-content {
  background-color: var(--PRIMARY__BG__COLOR);
  // border-radius: 12px !important;
}

.ant-input:focus, .ant-input-focused {
  border: 0 !important;
   border-right-width: 0px !important; 
   border-left-width: 0px !important; 
  outline: 0;
  -webkit-box-shadow: 0 0 0 0;
  /* -webkit-box-shadow: 0 0 0 2px rgb(24 144 255 / 20%); */
  /* box-shadow: 0 0 0 2px rgb(24 144 255 / 20%); */
}

.am-tabs-pane-wrap > div{
  width: 100vw;
}

.am-list-item.am-input-item{
  border-radius: 1.5rem;
  background-color: var(--BOTTOM__MODAL__COLOR);
}

.am-input-label-5{
  width: 34px !important;
}

.place_order_layout.am-list-item.am-input-item{
  background: transparent;
  height: unset;
  min-height: unset;
  font-size: var(--fs__small);
}

.place_order_layout input{
  text-align: center;
  font-size: 1rem !important;
  color: var(--PRIMARY__CONTENT__COLOR) !important
}

.place_order_layout .ant-select-selector{
  background-color: transparent !important;
    border: 0 !important;
}

.ant-select-focused .ant-select-selector,
.ant-select-selector:focus,
.ant-select-selector:active,
.ant-select-open .ant-select-selector {
  border-color: transparent !important;
  box-shadow: none !important;
}

.input-price-placeorder{
  padding: 0;
  border-radius: 0.5rem;
  border: 0 !important;
  width:75px !important;
  // min-width: 75px !important;
  .rc-virtual-list-holder-inner{
    padding: 0;
    background: var(--PRIMARY__BG__COLOR)
  }
  .ant-select-item-option{
    padding: 0.25rem 0;
    color: var(--PRIMARY__CONTENT__COLOR);
    font-size: 1rem;
    font-weight: 500;
    text-align: center;
  }
}


.ant-btn {
  border-color: unset;
  border-width: 0px;
}

.am-steps {
  width: auto;
  margin: 0 2rem;
}

.am-steps-item-finish .am-steps-item-tail:after {
  background-color: var(--PRIMARY)
}
.am-steps-item-tail:after {
  height: 3px;
  top: 3px;
}

.ant-input:hover {
  border-color: transparent;
}
.ant-picker-input > input {
    // font-size: 1.125rem;
    color: var(--PRIMARY__CONTENT__COLOR);
}
.ant-input-suffix {
  color: var(--PRIMARY__CONTENT__COLOR);
}

.ant-modal-footer {
  border-top-width: 0;
}

.ant-btn-ghost[disabled], .ant-btn-ghost[disabled]:hover, .ant-btn-ghost[disabled]:focus, .ant-btn-ghost[disabled]:active {
  border-width: 1px;
}