.modal-ekyc {
    .ant-modal-content {
        background-color: #122f41;
    }
}

.row-ekyc {
    display: flex;
    flex-direction: row;
    padding-top: 8px;
    padding-bottom: 8px;
    .ant-picker, .ant-select-selector {
        border: none!important;
    }
    input {
        padding-right: 31px;
        padding-left: 11px;
    }
    .ant-select {
        width: 100%;
    }
    .ant-picker-input > input,.ant-select {
        font-size: 1.125rem;
        color: var(--PRIMARY__CONTENT__COLOR);
    }
}

.preview-sign {
    height: 400;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}