.modal-ekyc {
    .ant-modal-content {
        background-color: #122f41;
    }
    .ant-modal-body {
        padding: 12px;
    }
}
.modal-ekyc-final {
    .ant-modal-body {
        padding: 12px;
    }
    .ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
        height: 26px;
    }
    .ant-picker.ant-picker-middle {
        width: 50%;
    }
    .ant-select {
        width: 50%;
    }
}

.row-ekyc {
    display: flex;
    flex-direction: row;
    padding-top: 4px;
    padding-bottom: 4px;
    .ant-picker, .ant-select-selector {
        border: none!important;
    }
    input {
        padding-right: 31px;
        padding-left: 11px;
    }
    .ant-picker-input input {
        padding-right: 0;
        padding-left: 0;
    }

    .ant-picker-input > input,.ant-select {
        font-size: 0.75rem;
    }
}

.preview-sign {
    height: 400;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}