.center__item {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  &--right {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: flex-end;
  }
  &--left {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: flex-start;
  }
  &--center {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: center;
  }
}

.cursor-default {
  cursor: default !important;
}
.cursor-pointer {
  cursor: pointer;
}
.cursor-text {
