}
.cursor-resize {
  cursor: ns-resize;
  cursor: url(data:image/gif;base64,R0lGODlhCQALAPAAAP///w9wuCH5BAEAAAAALAAAAAAJAAsAAAIUhBGnwYrcDJxvQrfs1UnS3DkhUAAAOw==),auto;
}

